import { mapGetters, mapActions } from "vuex";
import { api } from "@/utils/api.js";
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: "",
props: {},
components: {
XHeader
},
data() {
return {
show_reward:false,
isTabOne: true,
isTabTwo: false,
menuTop: '',
listObj: [],
page: 1,
type_num:0,
objData:{},
showIncomeModal:false,
fansContent:{
list:[
{
num:0,
},
{
num:0,
}
]
},
show_bottom:false,
redBackList: [],//收益红包弹窗数据
closeComsumeModal:false
};
},
mounted(){
this.$nextTick(function() {
this.fans_list();
this.fanStatistic();
});
},
methods: {
incomeModal(){
this.showIncomeModal=true;
},
closeIncomeModal(){
this.showIncomeModal=false;
},
lingquFun(){
this.closeComsumeModal=true;
},
closeReceiveModal() {
this.closeComsumeModal=false;
},
choseTab(e){
var isTabOne = !(this.isTabOne);
var isTabTwo = !(this.isTabTwo);
this.isTabOne=isTabOne;
this.isTabTwo=isTabTwo;

if (isTabOne) {//如果是我的粉丝

this.page=0;
this.listObj=[];
this.show_bottom=false;
this.fans_list();
} else {//如果是邀请排行榜
this.page=0;
this.listObj=[];
this.show_bottom=false;
this.rank_list();
}
},
async fanStatistic(){
let data={
plat: 3,
account: this.account,
token: this.token,
};
const [err, res] = await api.fancontentstatistic(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}else{
if(res.code==2000){
this.fansContent=res.data;
this.redBackList=res.data.redBackList;

}
}
},
async fans_list() {//这个是我的粉丝列表请求方法
let data={
plat: 3,
account: this.account,
token: this.token,
page: this.page,
type:1
};
this.page++;
const [err, res] = await api.storefan1(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}else{
if(res.code==2000){
this.listObj = this.listObj.concat(res.data.list);
this.total_count=res.data.total_count;
}
}
},
async rank_list() {//合伙人接口

let data={
plat: 3,
account: this.account,
token: this.token,
page: this.page,
type:2
};
this.page++;
const [err, res] = await api.storefan1(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}else{
if(res.code==2000){
this.listObj = this.listObj.concat(res.data.list);
this.total_count=res.data.total_count;
}
}

},

goFansDetail(e){





},



},
computed: {
...mapGetters(["user", "account", "token"])
}
};



